package pl.reszetow.businesscard.theme

import androidx.compose.material3.MaterialTheme
import androidx.compose.runtime.Composable
import androidx.compose.runtime.CompositionLocalProvider
import androidx.compose.runtime.staticCompositionLocalOf
import androidx.compose.ui.graphics.Color
import androidx.compose.ui.text.TextStyle
import androidx.compose.ui.text.font.FontWeight
import androidx.compose.ui.unit.dp
import androidx.compose.ui.unit.sp

object AppTheme {
    val typography = AppTypography
    val colors = AppColors
    val dimensions = Dimensions
}

// CompositionLocal for theme
val LocalAppTheme = staticCompositionLocalOf { AppTheme }

// CompositionLocal for mobile detection
val LocalIsMobile = staticCompositionLocalOf { false }

@Composable
fun AppThemeProvider(
    isMobile: Boolean = false,
    content: @Composable () -> Unit
) {
    MaterialTheme {
        CompositionLocalProvider(
            LocalAppTheme provides AppTheme,
            LocalIsMobile provides isMobile,
            content = content
        )
    }
}
