package pl.reszetow.businesscard.components

import androidx.compose.foundation.background
import androidx.compose.foundation.clickable
import androidx.compose.foundation.hoverable
import androidx.compose.foundation.interaction.MutableInteractionSource
import androidx.compose.foundation.interaction.collectIsHoveredAsState
import androidx.compose.foundation.layout.Box
import androidx.compose.foundation.layout.size
import androidx.compose.foundation.shape.CircleShape
import androidx.compose.material3.Icon
import androidx.compose.runtime.Composable
import androidx.compose.runtime.getValue
import androidx.compose.runtime.remember
import androidx.compose.ui.Alignment
import androidx.compose.ui.Modifier
import androidx.compose.ui.draw.clip
import androidx.compose.ui.graphics.painter.Painter
import pl.reszetow.businesscard.theme.AppColors
import pl.reszetow.businesscard.theme.ComponentDimensions

@Composable
fun ContactIconButton(
    icon: Painter,
    onClick: () -> Unit = {}
) {
    val interactionSource = remember { MutableInteractionSource() }
    val isHovered by interactionSource.collectIsHoveredAsState()

    val backgroundColor = if (isHovered) {
        AppColors.ContactButtonHover
    } else {
        AppColors.ContactButtonDefault
    }

    Box(
        modifier = Modifier
            .size(ComponentDimensions.ContactButtonSize)
            .clip(CircleShape)
            .background(backgroundColor)
            .hoverable(interactionSource)
            .clickable { onClick() },
        contentAlignment = Alignment.Center
    ) {
        Icon(
            painter = icon,
            contentDescription = null,
            modifier = Modifier.size(ComponentDimensions.ContactIconSize),
            tint = AppColors.IconTint
        )
    }
}
