package pl.reszetow.businesscard.components

import androidx.compose.foundation.Image
import androidx.compose.foundation.layout.*
import androidx.compose.foundation.shape.CircleShape
import androidx.compose.material3.Text
import androidx.compose.runtime.Composable
import androidx.compose.ui.Modifier
import androidx.compose.ui.draw.clip
import androidx.compose.ui.layout.ContentScale
import androidx.compose.ui.text.font.FontWeight
import org.jetbrains.compose.resources.painterResource
import businesscard.composeapp.generated.resources.Res
import businesscard.composeapp.generated.resources.facebook_icon
import businesscard.composeapp.generated.resources.linkedin_icon
import businesscard.composeapp.generated.resources.mail_icon
import businesscard.composeapp.generated.resources.profile_image
import businesscard.composeapp.generated.resources.stackoverflow_icon
import businesscard.composeapp.generated.resources.name
import businesscard.composeapp.generated.resources.skills_title
import businesscard.composeapp.generated.resources.skills_list
import businesscard.composeapp.generated.resources.contact_me_title
import org.jetbrains.compose.resources.stringResource
import pl.reszetow.businesscard.theme.LocalAppTheme
import pl.reszetow.businesscard.theme.LocalIsMobile
import pl.reszetow.businesscard.data.ContactInfo
import pl.reszetow.businesscard.externals.openUrl

@Composable
fun PersonalInfoSection() {
    val theme = LocalAppTheme.current
    val isMobile = LocalIsMobile.current
    Column(
        modifier = if (isMobile) {
            Modifier.fillMaxWidth()
        } else {
            Modifier.width(theme.dimensions.component.PersonalInfoWidth)
        },
        verticalArrangement = Arrangement.SpaceBetween
    ) {
        Column {
            ProfileImageAndName()
            Spacer(modifier = Modifier.padding(vertical = theme.dimensions.component.VerticalPadding))
            Skills()
            Spacer(modifier = Modifier.height(theme.dimensions.spacer.SpacingLarge))
            ContactMe()
        }
    }
}

@Composable
private fun ProfileImageAndName() {
    val theme = LocalAppTheme.current
    Image(
        painter = painterResource(Res.drawable.profile_image),
        contentDescription = "Profile picture",
        modifier = Modifier
            .size(theme.dimensions.component.ProfileImageSize)
            .clip(CircleShape),
        contentScale = ContentScale.Crop
    )

    Spacer(modifier = Modifier.height(theme.dimensions.spacer.SpacingLarge))

    Text(
        text = stringResource(Res.string.name),
        style = theme.typography.headingMedium
    )
}

@Composable
private fun Skills() {
    val theme = LocalAppTheme.current
    Text(
        text = stringResource(Res.string.skills_title),
        style = theme.typography.headingSmall
    )
    Spacer(modifier = Modifier.height(theme.dimensions.spacer.SpacingTiny))
    Text(
        text = stringResource(Res.string.skills_list),
        style = theme.typography.bodyMedium
    )
}

@Composable
private fun ContactMe() {
    val theme = LocalAppTheme.current
    Text(
        text = stringResource(Res.string.contact_me_title),
        style = theme.typography.headingSmall
    )

    Spacer(modifier = Modifier.height(theme.dimensions.spacer.SpacingSmall))

    Row(
        horizontalArrangement = Arrangement.spacedBy(theme.dimensions.spacer.SpacingMedium)
    ) {
        ContactIconButton(
            icon = painterResource(Res.drawable.linkedin_icon),
            onClick = { openUrl(ContactInfo.LINKEDIN_URL) }
        )

        ContactIconButton(
            icon = painterResource(Res.drawable.mail_icon),
            onClick = { openUrl(ContactInfo.EMAIL) }
        )

        ContactIconButton(
            icon = painterResource(Res.drawable.stackoverflow_icon),
            onClick = { openUrl(ContactInfo.STACKOVERFLOW_URL) }
        )

        ContactIconButton(
            icon = painterResource(Res.drawable.facebook_icon),
            onClick = { openUrl(ContactInfo.FACEBOOK_URL) }
        )
    }
}
